.home__main{
    // height:100vh;
    // padding:5rem auto 0;
    .home__main-clipart{
        @media screen and (min-width:720px){
            // position:absolute;
            position:fixed;
            width:100%;
            height:100%;
            background:rgb(241, 242, 245);
            z-index:-1000;
            clip-path: polygon(100% 0, 100% 50%, 0 100%, 0 0);
            // clip-path: polygon(0 0, 100% 0, 52% 80%, 0 80%);
            // clip-path: polygon(50% 0%, 100% 0, 98% 13%, 95% 25%, 90% 38%, 83% 50%, 73% 61%, 0 75%, 0% 35%, 0 0);
        }
    }
    .self-intro__container{
        width:100%;
        text-align: center;
        display:flex;
        justify-content: center;
        align-items:center;
        z-index:1000;
        .self-intro{
            margin-top:5rem;
            height:auto;
            @media screen and (min-width:720px){
                margin-top: 8rem;
                display:grid;
                grid-template-columns: 1fr 1fr;
                grid-template-rows: 0.7fr 0.5fr 0.5fr;
            }
            &:first-child{
                @media screen and (min-width:720px){
                    grid-row: 1/2;
                    grid-column: 1/2;
                    // align-self: center;
                }
                @media screen and (min-width:1200px){
                    padding-left:2rem ;
                }
                // @media screen and (max-width:1055px){
                //     padding-left:3rem ;
                // }
                h1{
                    text-align:center;
                    margin-bottom:0;
                    // transform: translateX(-100%);
                    transition: transform 1s ease-out;
                    @media screen and (min-width:720px){
                        font-size:2.5em;
                    }
                    @media screen and (min-width:920px){
                        font-size:3em;
                    }
                }
                h3{
                    margin-top:0;
                    margin-bottom:0;
                    // transform: translateY(-100%);
                    transition: transform 1s ease-out;
                    span{
                        color:#f50057;
                    }
                    @media screen and (min-width:720px){
                        font-size:1.4em;
                    }
                }
            }
            .self-intro__image{
                width:50%;
                margin:auto;
                .css-1sxbfa7{
                    margin:auto;
                }
                img{
                    max-width:100%;
                    // height:auto;
                    border-radius:50%;
                    // animation: 1s ease-in-out 0s 1 normal none running bounceIn;

                    // @keyframes bounceIn {
                
                    //     0% {
                    //         transform: scale3d(.3,.3,.3);
                    //     }
                    //     20% {
                    //         transform: scale3d(1.1,1.1,1.1);
                    //     }
                    //     40% {
                    //         transform: scale3d(.9,.9,.9);
                    //     }
                    //     60% {
                    //         transform: scale3d(1.03,1.03,1.03);
                    //     }
                    //     80% {
                    //         transform: scale3d(.97,.97,.97);
                    //     }
                    //     100% {
                    //         transform: scale3d(1,1,1);
                    //     }
                    // }
                    @media screen and (min-width:720px){
                        border-radius: 5px;
                    }
                }
                @media screen and (min-width: 720px){
                    grid-column:2/3;
                    grid-row:1/3;
                    width:50%;    
                    align-self: center;   
                }
            }
            p{
                font-size: 1.05em;
                margin-top:0;
                padding:1rem 1rem 0;
                // transform: translateY(-20%);
                transition: transform 1s ease-out;
                // @media screen and (min-width:500px){
                    // transform: translateY(-30%);
                    // transition: transform 1s ease-out;
                // }
                @media screen and (min-width: 720px){
                    grid-row: 2/4;
                    grid-column: 1/2;
                    padding:0.8rem 2rem 0;       
                }
                @media screen and (min-width:1200px){
                    // padding:1.5rem 3rem 0;
                    padding-left:5rem ;
                }
            }
            &:last-child{
                & button:nth-of-type(1){
                    a{
                        text-decoration:none;
                        color:inherit;
                    }
                }
                @media screen and (min-width:720px){
                    grid-column:2/3;
                    grid-row:3/4;
                }
            }
        }
        @media screen and (min-width:720px){
            height:100%;
            padding-top:5rem;
        }
        // @media screen and(min-width:820px){
        
        // }
    }
}